*{
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
  font-family: 'Open Sans', system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fefefe;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.regex-segment-container {
  min-width: 200;
  border: 1px solid #cdcdcd;
  background-color: #fff;
  padding: 4px 8px;
  border-radius: 6px;
}
.regex-segments{
  overflow-x: auto;
}
.regex-output-container{
  color: #2563EB;
  display: inline-flex;
  align-items: center;
  gap: 8px;
  background-color: #efefef;
  padding: 4px 8px;
  border-radius: 4px;
  margin-top: 8px;
}
.regex-segment{
  font-size: 14px;
  font-weight: 400;
  white-space: nowrap;
}
.regex-segment-fixed{
  background-color: #efefef !important;
}


.regex-segment-chip{
  position: relative;
  padding: 2px 12px;
  border-radius: 3px;
  cursor: pointer;
  background-color: #ebebeb;
}

.oops-not-found {
  font-size: 120px;
  font-weight: 900;
  background-image: url("./assets/images/rTZW4f02zY8.jpg");
  background-size: cover;
  background-position: center;
  font-size: 10rem;
  font-weight: bold;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.app-wrapper{
  padding: 18px 0px;
}